.OpElement {
  color: #0078d4;
  text-decoration: initial;

  &.alt {
    cursor: pointer;
    user-select: none;
  }
  &.selected {
    box-shadow: 0 0 0 2px #fbc02d;
    // background-color: #bcd8fd;
    border-radius: 4px;
  }

  &:hover,
  &:hover {
    color: #004578;
    text-decoration: underline;
  }
}
