.screenshot {
  display: flex;
  justify-content: center;
  position: relative;

  margin-top: 1rem;
  margin-left: 1rem;
  margin-right: 1rem;
  padding-top: 20px;

  &:hover {
    & > .toolbars {
      display: block;
    }

    box-shadow: 0px 4px 5px -2px rgba(0, 0, 0, 0.2),
      0px 7px 10px 1px rgba(0, 0, 0, 0.14), 0px 2px 16px 1px rgba(0, 0, 0, 0.12);
    transition: box-shadow 300ms cubic-bezier(0.4, 0, 0.2, 1) 0ms;
    border-radius: 4px;
  }
  & > .toolbars {
    display: none;
    position: absolute;
    right: 8px;
    top: -8px;
  }
}

.dep-svg {
  position: absolute;
  width: 100%;
  height: 100%;
  left: 0;
  top: 0;
  pointer-events: none;
  .dep {
    stroke: red;
    stroke-width: 1px;
    fill: none;
  }
  text {
    font-size: xx-small;
  }
}

foreignObject {
  pointer-events: auto;
}

.source-wrapper {
  display: flex;
  justify-content: space-around;
  align-items: center;
  height: 100%;
  width: 100%;
}

.source {
  width: 20px;
  height: 20px;

  background-color: steelblue;
  border-radius: 50%;
  transition: all 200ms;
}

.source.active,
.source:hover {
  width: 50%;
  height: 100%;
  border-radius: 8px;
}

iframe {
  position: fixed;
  bottom: 0;
  visibility: hidden;
}

::-webkit-scrollbar {
  width: 15px;
}

::-webkit-scrollbar-track {
  background-color: transparent;
}

::-webkit-scrollbar-thumb {
  background-color: #d6dee1;
  border-radius: 10px;
  border: 6px solid transparent;
  background-clip: content-box;
}

::-webkit-scrollbar-thumb:hover {
  background-color: #a8bbbf;
}

