.slate-HintSelect {
  top: -9999px;
  left: -9999px;
  position: absolute;
  z-index: 1;
  padding: 3px;
  background: white;
  border-radius: 4px;
  box-shadow: 0 1px 5px rgba(0, 0, 0, 0.2);

  .hint-item {
    padding: 1px 3px;
    border-radius: 3px;
    background: transparent;
    cursor: pointer;

    text-align: left;
    display: flex;
    justify-content: space-between;
    align-items: flex-end;
    gap: 8px;

    & .value {
      font-size: smaller;
      color: gray;

      & .query {
        font-size: medium;
        font-weight: 500;
        color: black;
      }
    }

    & .more {
      font-size: 0.8rem;
      color: #999;
      vertical-align: bottom;
    }

    &.selected {
      background: #b4d5ff;
    }
  }

  .hintPreview {
    position: absolute;
    // background-color: red;
    // right: -101px;
    padding: 3px;
    background: white;
    border-radius: 4px;
    box-shadow: 0 1px 5px rgba(0, 0, 0, 0.2);
  
    left: calc(2px + 100%);
    top: 0;
    & > img {
      width: 250px;
    }
  }
}
