.table {
  border-collapse: separate !important;
  table-layout: fixed;
  padding: 0 8px;
  box-sizing: border-box;

  .head {
    width: 50px;
    &.upper {
      padding: 0;
      text-align: center;
      text-overflow: clip !important;
      // min-height: 50px;
    }
  }

  .cell {
    white-space: nowrap;
    text-overflow: ellipsis;
    overflow: hidden;
    // display: inline-flex;
    padding: 6px 8px 6px 8px;

    &.highlighted-h {
      border-top: 2px dashed steelblue;
      border-bottom: 2px dashed steelblue;
    }

    &.highlighted-v {
      border-left: 2px dashed steelblue;
      border-right: 2px dashed steelblue;
    }

    &.highlighted-h.highlighted-v {
      background-color: lightblue;
    }

    &.focused {
      background-color: lightcoral;
    }
    &.editing {
      border: 2px solid pink;
    }
  }

  .ctrl {
    padding: 0;
  }
  .virtual {
    animation: fadein 1s;
    background-color: #e5e5f733;
    // opacity: 0.4;
    background: repeating-linear-gradient(
      -45deg,
      #7ebbe833,
      #7ebbe833 2px,
      #e5e5f733 2px,
      #e5e5f733 10px
    );

    &.cell:not(.ctrl),
    & > :not(.ctrl) {
      opacity: 0.6;
    }
  }
}

@keyframes fadein {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
