.Dropdown-root {
  position: relative;
}

.Dropdown-control {
  position: relative;
  overflow: hidden;
//   background-color: white;
//   border: 1px solid #ccc;
//   border-radius: 2px;
  box-sizing: border-box;
  color: #333;
  cursor: default;
  outline: none;

  //   padding: 8px;
  padding: 3px 3px 2px;
  border-radius: 4px;
  vertical-align: baseline;
  background-color: rgb(238, 238, 238);
  font-size: 0.9em;

  transition: all 200ms ease;

  &:hover {
    box-shadow: 0 1px 0 rgba(0, 0, 0, 0.06);
  }
}

.Dropdown-arrow {
  border-color: #999 transparent transparent;
  border-style: solid;
  border-width: 5px 5px 0;
  content: " ";
  display: block;
  height: 0;
  margin-top: -ceil(2.5);
  position: absolute;
  right: 10px;
  top: 14px;
  width: 0;
}

.is-open .Dropdown-arrow {
  border-color: transparent transparent #999;
  border-width: 0 5px 5px;
}

.Dropdown-menu {
  background-color: white;
  border: 1px solid #ccc;
  box-shadow: 0 1px 0 rgba(0, 0, 0, 0.06);
  box-sizing: border-box;
  margin-top: -1px;
  max-height: 200px;
  overflow-y: auto;
  position: absolute;
  top: 100%;
  left: -4px;
  padding: 0 4px;

//   width: 100%;
  z-index: 1000;
  -webkit-overflow-scrolling: touch;
  .Dropdown-group > .Dropdown-title {
    padding: 8px 10px;
    color: rgba(51, 51, 51, 1);
    font-weight: bold;
    text-transform: capitalize;
  }
}

.Dropdown-option {
  box-sizing: border-box;
  color: rgba(51, 51, 51, 0.8);
  cursor: pointer;
  display: block;
  text-align: center;
  padding: 4px 0px;
  font-size: 0.9em;

  &:last-child {
    border-bottom-right-radius: 2px;
    border-bottom-left-radius: 2px;
  }
  &:hover {
    background-color: #f2f9fc;
    color: #333;
  }
  &.is-selected {
    background-color: rgb(238, 238, 238); // #f2f9fc;
    color: #333;
  }
}

.Dropdown-noresults {
  box-sizing: border-box;
  color: #ccc;
  cursor: default;
  display: block;
  padding: 8px 10px;
}
