.panel {
  // border: 1px solid #313131;
  box-sizing: border-box;
  display: flex;
  flex-direction: column;

  &.border {
    border: 4px solid palevioletred;
  }

  & > .header {
    display: flex;
    justify-content: space-between;
    border-bottom: 1px #979797 solid;
    min-height: 48px;

    & > .title {
      // background-color: wheat;
      margin: 0;
      display: flex;
      flex-direction: column;
      justify-content: center;
      margin-left: 1rem;
      font-weight: 700;
    }

    & > .toolbars {
    }
  }

  & > .wrapper {
    width: 100%;
    height: 100%;
    overflow: auto;
    padding-bottom: 1rem;
  }
}
