@import url(https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,100;0,300;0,400;0,500;0,700;0,900;1,100;1,300;1,400;1,500;1,700;1,900&display=swap);
body{background-color:#f8f9fa;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}.App{font-family:"Roboto", sans-serif;background-color:#f5f5f5;display:flex;grid-gap:1rem;gap:1rem;align-items:flex-start;padding-bottom:4px;border-bottom:1px solid #676767}.App .colRegion{overflow:auto;height:calc(98vh - 8px)}.App .rawData{width:23%;max-width:600px;display:flex;flex-direction:column;justify-content:space-between}.App .insights{width:38%;max-width:750px}.App .insights .insights-inner{grid-gap:10px;gap:10px;display:flex;flex-direction:column}.App .document{width:40%;max-width:750px}.App .insight-list{margin-left:1rem}.App .insight-list>.title{margin-bottom:0}.info{width:-webkit-fit-content;width:-moz-fit-content;width:fit-content}.screenshot{display:flex;justify-content:center;position:relative;margin-top:1rem;margin-left:1rem;margin-right:1rem;padding-top:20px}.screenshot:hover{box-shadow:0px 4px 5px -2px rgba(0,0,0,0.2),0px 7px 10px 1px rgba(0,0,0,0.14),0px 2px 16px 1px rgba(0,0,0,0.12);transition:box-shadow 300ms cubic-bezier(0.4, 0, 0.2, 1) 0ms;border-radius:4px}.screenshot:hover>.toolbars{display:block}.screenshot>.toolbars{display:none;position:absolute;right:8px;top:-8px}.dep-svg{position:absolute;width:100%;height:100%;left:0;top:0;pointer-events:none}.dep-svg .dep{stroke:red;stroke-width:1px;fill:none}.dep-svg text{font-size:xx-small}foreignObject{pointer-events:auto}.source-wrapper{display:flex;justify-content:space-around;align-items:center;height:100%;width:100%}.source{width:20px;height:20px;background-color:steelblue;border-radius:50%;transition:all 200ms}.source.active,.source:hover{width:50%;height:100%;border-radius:8px}iframe{position:fixed;bottom:0;visibility:hidden}::-webkit-scrollbar{width:15px}::-webkit-scrollbar-track{background-color:transparent}::-webkit-scrollbar-thumb{background-color:#d6dee1;border-radius:10px;border:6px solid transparent;background-clip:content-box}::-webkit-scrollbar-thumb:hover{background-color:#a8bbbf}

.chartWrapper{width:100%;height:100%;padding:0 8px;box-sizing:border-box}.chartWrapper .controls{display:flex;grid-gap:1rem;gap:1rem;padding-left:40px}.chartWrapper .unFocused{opacity:0.6}

.table{border-collapse:separate}.table .cell.highlighted-h{border-top:2px dashed steelblue;border-bottom:2px dashed steelblue}.table .cell.highlighted-v{border-left:2px dashed steelblue;border-right:2px dashed steelblue}.table .cell.highlighted-h.highlighted-v{background-color:lightblue}.table .cell.focused{background-color:lightcoral}

.table{border-collapse:separate !important;table-layout:fixed;padding:0 8px;box-sizing:border-box}.table .head{width:50px}.table .head.upper{padding:0;text-align:center;text-overflow:clip !important}.table .cell{white-space:nowrap;text-overflow:ellipsis;overflow:hidden;padding:6px 8px 6px 8px}.table .cell.highlighted-h{border-top:2px dashed steelblue;border-bottom:2px dashed steelblue}.table .cell.highlighted-v{border-left:2px dashed steelblue;border-right:2px dashed steelblue}.table .cell.highlighted-h.highlighted-v{background-color:lightblue}.table .cell.focused{background-color:lightcoral}.table .cell.editing{border:2px solid pink}.table .ctrl{padding:0}.table .virtual{-webkit-animation:fadein 1s;animation:fadein 1s;background-color:#e5e5f733;background:repeating-linear-gradient(-45deg, rgba(126,187,232,0.2), rgba(126,187,232,0.2) 2px, rgba(229,229,247,0.2) 2px, rgba(229,229,247,0.2) 10px)}.table .virtual.cell:not(.ctrl),.table .virtual>:not(.ctrl){opacity:0.6}@-webkit-keyframes fadein{from{opacity:0}to{opacity:1}}@keyframes fadein{from{opacity:0}to{opacity:1}}

.word-processor{display:flex;flex-direction:column;padding:2rem;box-sizing:border-box;height:-webkit-fit-content;height:-moz-fit-content;height:fit-content;margin:0 1rem}.word-processor .dragging{cursor:col-resize}

.slate-HintElement{margin:0 1px;vertical-align:baseline;display:inline-block;padding:3px 0px;border-radius:4px;color:#313131;background-image:linear-gradient(white, rgba(188,216,253,0.5));font-size:0.9em;cursor:pointer;position:relative;-webkit-user-select:none;-ms-user-select:none;user-select:none;box-shadow:none}.slate-HintElement:hover,.slate-HintElement.selected.focused{box-shadow:0 0 0 2px #fbc02d;background-color:#bcd8fd;background-image:linear-gradient(#bcd8fd, #bcd8fd)}.slate-HintElement.placeholder,.slate-HintElement.dep{background-color:#eee;background-image:linear-gradient(#eee, #eee);cursor:default}[data-tooltip]{display:inline-block;position:relative}[data-tooltip]:before{content:attr(data-tooltip);display:none;position:absolute;background:#313133;color:#fff;padding:8px;font-size:14px;width:-webkit-max-content;width:max-content;text-align:center;border-radius:4px}[data-tooltip-position="top"]:before{left:50%;-webkit-transform:translateX(-50%);transform:translateX(-50%)}[data-tooltip-position="top"]:before{bottom:100%;margin-bottom:6px}[data-tooltip]:after{content:"";display:none;position:absolute;width:0;height:0;border-color:transparent;border-style:solid}[data-tooltip-position="top"]:after{left:50%;margin-left:-6px}[data-tooltip-position="top"]:after{bottom:100%;border-width:6px 6px 0;border-top-color:#000}[data-tooltip]:hover:before,[data-tooltip]:hover:after{display:block;z-index:50}

.Dropdown-root{position:relative}.Dropdown-control{position:relative;overflow:hidden;box-sizing:border-box;color:#333;cursor:default;outline:none;padding:3px 3px 2px;border-radius:4px;vertical-align:baseline;background-color:#eee;font-size:0.9em;transition:all 200ms ease}.Dropdown-control:hover{box-shadow:0 1px 0 rgba(0,0,0,0.06)}.Dropdown-arrow{border-color:#999 transparent transparent;border-style:solid;border-width:5px 5px 0;content:" ";display:block;height:0;margin-top:-ceil(2.5);position:absolute;right:10px;top:14px;width:0}.is-open .Dropdown-arrow{border-color:transparent transparent #999;border-width:0 5px 5px}.Dropdown-menu{background-color:white;border:1px solid #ccc;box-shadow:0 1px 0 rgba(0,0,0,0.06);box-sizing:border-box;margin-top:-1px;max-height:200px;overflow-y:auto;position:absolute;top:100%;left:-4px;padding:0 4px;z-index:1000;-webkit-overflow-scrolling:touch}.Dropdown-menu .Dropdown-group>.Dropdown-title{padding:8px 10px;color:#333;font-weight:bold;text-transform:capitalize}.Dropdown-option{box-sizing:border-box;color:rgba(51,51,51,0.8);cursor:pointer;display:block;text-align:center;padding:4px 0px;font-size:0.9em}.Dropdown-option:last-child{border-bottom-right-radius:2px;border-bottom-left-radius:2px}.Dropdown-option:hover{background-color:#f2f9fc;color:#333}.Dropdown-option.is-selected{background-color:#eee;color:#333}.Dropdown-noresults{box-sizing:border-box;color:#ccc;cursor:default;display:block;padding:8px 10px}

.slate-HintSelect{top:-9999px;left:-9999px;position:absolute;z-index:1;padding:3px;background:white;border-radius:4px;box-shadow:0 1px 5px rgba(0,0,0,0.2)}.slate-HintSelect .hint-item{padding:1px 3px;border-radius:3px;background:transparent;cursor:pointer;text-align:left;display:flex;justify-content:space-between;align-items:flex-end;grid-gap:8px;gap:8px}.slate-HintSelect .hint-item .value{font-size:smaller;color:gray}.slate-HintSelect .hint-item .value .query{font-size:medium;font-weight:500;color:black}.slate-HintSelect .hint-item .more{font-size:0.8rem;color:#999;vertical-align:bottom}.slate-HintSelect .hint-item.selected{background:#b4d5ff}.slate-HintSelect .hintPreview{position:absolute;padding:3px;background:white;border-radius:4px;box-shadow:0 1px 5px rgba(0,0,0,0.2);left:calc(2px + 100%);top:0}.slate-HintSelect .hintPreview>img{width:250px}

.OpElement{color:#0078d4;text-decoration:none;text-decoration:initial}.OpElement.alt{cursor:pointer;-webkit-user-select:none;-ms-user-select:none;user-select:none}.OpElement.selected{box-shadow:0 0 0 2px #fbc02d;border-radius:4px}.OpElement:hover,.OpElement:hover{color:#004578;text-decoration:underline}

.mapWrapper{width:100%;height:100%;padding:0 8px;padding-bottom:8px;box-sizing:border-box}.mapWrapper .controls{display:flex;grid-gap:1rem;gap:1rem;padding-left:40px}.mapWrapper .country:hover{opacity:0.5}

.panel{box-sizing:border-box;display:flex;flex-direction:column}.panel.border{border:4px solid palevioletred}.panel>.header{display:flex;justify-content:space-between;border-bottom:1px #979797 solid;min-height:48px}.panel>.header>.title{margin:0;display:flex;flex-direction:column;justify-content:center;margin-left:1rem;font-weight:700}.panel>.wrapper{width:100%;height:100%;overflow:auto;padding-bottom:1rem}

.chartWrapper{width:100%;height:100%;padding:0 8px;box-sizing:border-box}.chartWrapper .lines{fill:none;stroke-width:3;stroke-linejoin:round;stroke-linecap:round}.chartWrapper .dot>text{font-family:sans-serif;font-size:10;text-anchor:middle}

.instruction{padding:8px;background-color:#fafafa;min-height:500px;display:flex;flex-direction:column;justify-content:space-between;border:1px solid}.instruction .instructionText{font-size:1.1rem;line-height:1.4rem;font-family:'Roboto'}.instruction .instructionText>.title{font-size:1.3rem;font-weight:600;margin-bottom:12px}.instruction .instructionText>.background{margin-bottom:12px}.instruction .instructionText>.action{margin-bottom:12px}.instruction .instructionText>.action>ol{margin-top:4px;padding-left:24px}.instruction .instructionText>.content{margin-top:24px;margin-bottom:24px;text-decoration:underline;color:#1769aa;font-weight:500;font-size:large;text-align:center}.instruction .instructionText>.ps>ol{margin-top:4px;padding-left:20px}.instruction .instructionText .label{font-weight:600}

