.slate-HintElement {
  margin: 0 1px;
  vertical-align: baseline;
  display: inline-block;
  padding: 3px 0px;
  border-radius: 4px;
  color: #313131;
  // background-color: #ccddf381;
  background-image: linear-gradient(white, rgba(188, 216, 253, 0.5));

  font-size: 0.9em;
  cursor: pointer;
  position: relative;
  user-select: none;
  box-shadow: none;

  &:hover,
  &.selected.focused {
    box-shadow: 0 0 0 2px #fbc02d;
    background-color: #bcd8fd;
    background-image: linear-gradient(#bcd8fd, #bcd8fd);
  }

  &.placeholder,
  &.dep {
    background-color: #eee;
    background-image: linear-gradient(#eee, #eee);;
    cursor: default;
  }
}

[data-tooltip] {
  display: inline-block;
  position: relative;
  // padding: 4px;
}
/* Tooltip styling */
[data-tooltip]:before {
  content: attr(data-tooltip);
  display: none;
  position: absolute;
  background: #313133;
  color: #fff;
  padding: 8px;
  font-size: 14px;
  // line-height: 1.4;
  // min-width: 100px;
  width: max-content;
  text-align: center;
  border-radius: 4px;
}
/* Dynamic horizontal centering */
[data-tooltip-position="top"]:before {
  left: 50%;
  transform: translateX(-50%);
}

[data-tooltip-position="top"]:before {
  bottom: 100%;
  margin-bottom: 6px;
}

/* Tooltip arrow styling/placement */
[data-tooltip]:after {
  content: "";
  display: none;
  position: absolute;
  width: 0;
  height: 0;
  border-color: transparent;
  border-style: solid;
}
/* Dynamic horizontal centering for the tooltip */
[data-tooltip-position="top"]:after {
  left: 50%;
  margin-left: -6px;
}

[data-tooltip-position="top"]:after {
  bottom: 100%;
  border-width: 6px 6px 0;
  border-top-color: #000;
}

/* Show the tooltip when hovering */
[data-tooltip]:hover:before,
[data-tooltip]:hover:after {
  display: block;
  z-index: 50;
}
