
.table {
  border-collapse: separate;
  .cell {
    &.highlighted-h {
      border-top: 2px dashed steelblue;
      border-bottom: 2px dashed steelblue;
    }
    &.highlighted-v {
      border-left: 2px dashed steelblue;
      border-right: 2px dashed steelblue;
    }
    &.highlighted-h.highlighted-v {
      background-color: lightblue;
    }
    &.focused {
      background-color: lightcoral;
      // font-weight: 700;
      // color: darkcyan;
    }
  }
}
