.instruction {
  padding: 8px;
  background-color: #fafafa;
  min-height: 500px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  border: 1px solid;

  .instructionText {
    font-size: 1.1rem;
    line-height: 1.4rem;
    font-family: 'Roboto';

    & > .title {
      font-size: 1.3rem;
      font-weight: 600;
      margin-bottom: 12px;
    }
    & > .background {
      margin-bottom: 12px;
    }
    & > .action {
      margin-bottom: 12px;
      & > ol {
        margin-top: 4px;
        padding-left: 24px;
      }
    }
    & > .content {
      margin-top: 24px;
      margin-bottom: 24px;
      text-decoration: underline;
      color: #1769aa;
      font-weight: 500;
      font-size: large;
      text-align: center;
    }
    & > .ps {
      & > ol {
        margin-top: 4px;
        padding-left: 20px;
      }
    }
    & .label {
      font-weight: 600;
      // text-decoration: underline;
    }
  }
}
