.chartWrapper {
  width: 100%;
  height: 100%;
  padding: 0 8px;
  box-sizing: border-box;
  .lines {
    fill: none;
    // stroke: steelblue;
    stroke-width: 3;
    stroke-linejoin: round;
    stroke-linecap: round;
    // path {
